@page {
    size: A4 portrait !important;
    margin: 0 !important;
}

@media print {
    .ScreenOnly {
        display: none;
    }
    .PrintOnly {
        display: block !important;
    }
    .PrintOutput {
        counter-reset: Page;
    }
    .tablePage, .pageDynamic {
        width: 100% !important;
    }
    .pageDynamic > * > *{
        width: 133% !important;
    }
    .dpl-table > thead > tr > td:nth-child(3), .dpl-table > thead > tr > td:nth-child(4), .dpl-table > thead > tr > td:nth-child(5),
    .dpl-table > tbody > tr > td:nth-child(3), .dpl-table > tbody > tr > td:nth-child(4), .dpl-table > tbody > tr > td:nth-child(5) {
        border: 1px solid black !important;
    }
    .dpl-table > thead > tr > td:nth-child(2), .dpl-table > tbody > tr > td:nth-child(2) {
        border-bottom: 1px solid black !important;
        border-top: 1px solid black !important;
    }
    .dpl-table > thead > tr > td:last-child, .dpl-table > tbody > tr > td:nth-last-child(2) {
        border-bottom: 1px solid black !important;
        border-top: 1px solid black !important;
        border-right: 1px solid white !important;
    }
    .dpl-table > thead > tr > td > pre {
        font-weight: bold;
    }
    .dpl-table > thead > tr > td > pre, .dpl-table > tbody > tr > td > pre{
        font-family: 'Open Sans', Arial;
        font-size: 0.5cm !important;
        color: black !important;
        border: 0 !important;
        padding: 0.2cm 0.4cm;
        text-align: left;
    }
    .dpl-table > tbody > tr:first-child {
        display: none;
    }
    .Page-Header {
        border-bottom: 1px solid black;
        margin-bottom: 0.2cm;
        display: flex;
    }
    .Page-Logo {
        width: 30%;
    }
    .Page-Detail {
        width: 70%;
    }
    .Page-Detail > div {
        text-align: center;
        text-transform: uppercase;
        padding: 0.2cm 0.5cm;
        font-size: 0.5cm;
    }
    .Page-Detail > div:nth-child(2) {
        border-top: 1px solid black;
        border-bottom: 1px solid black;
    }
    .Page-Footer {
        border-top: 1px solid black;
        justify-content: space-between;
        text-transform: uppercase;
        padding: 0.5cm 0.5cm;
        margin-top: 0.2cm;
        font-size: 0.4cm;
        display: flex;
    }
    .Page:after {
        counter-increment: Page;
        /*content: " " counter(Page);*/
    }

    .printContent {
        height: 316mm;
        border: 1px solid white;    /* White-border */
    }
    .Page-Heading1 {
        text-transform: uppercase;
        font-weight: bold;
        font-size: 0.5cm;
        padding: 0 0.5cm 0.1cm 0.5cm;
        margin: 0 !important;
    }
    .Page-Heading2 {
        text-transform: uppercase;
        font-size: 0.5cm;
        padding: 0 0.5cm 0.1cm 0.5cm;
        margin: 0 !important;
        text-align: left;
    }
    #CurrentView {
        position: relative;
        margin: 45mm 3mm !important;

        /* change will scale the 3d image (total height 316)*/
        height: 220mm !important;
        /*padding: 48mm 0;*/
    }
    #partsTable {
        position: relative;
    }
    .pageDynamic {
        display: none;
    }
}

@media screen {
    .ScreenOnly {
        display: block;
    }
    .PrintOnly, .PrintOutput {
        display: none !important;
    }
}