:root {
    --indent-unit: 5px;
}

#root {
    text-align: center;
    overflow: hidden;
}

.app {
    text-align: center;
    overflow: hidden;
    height: 100vh;
    width: 100vw;
}

#procedure-holder {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    overflow: auto;
    padding: .5em;
}

