.tiramisu-proc-item {
    font-family: 'Open Sans', Arial;
    font-size: 12pt;
    text-align: left;
    color: white
}

.tiramisu-proc-item.selected {
    background-color: #21608D;
    border: 1px solid #21608D;
}

.tiramisu-proc-item:hover {
    background-color: #21608D;
    border: 1px solid #21608D;
}

.tiramisu-proc-title, h1[data-class="- topic/title "] {
    color: white;
    font-weight: bold;
    font-size: 36px;
}

.tiramisu-proc-item-number, .tiramisu-proc-item-content, .Procedure {
    color: white;
    text-align: left;
    margin-left: 1em;
    font-size: 18px;
}

.MuiDialog-paperWidthMd {
    width: 500px;
}

.MuiButton-root {
    font-weight: bold !important;
    line-height: 1.25 !important;
}

img[data-class="- topic/image "] {
    text-align: center !important;
    display: block;
    margin: 0 auto;
}


.stepexpand.selected {
    background-color: #21608D;
    border: 1px solid #21608D;
}

.SelectMenu {
    border: none;
}

.MuiDialog-paper {
    width: 1500px !important;
}

.MuiDialog-paperWidthSm {
    max-width: -webkit-fill-available !important;
}

table, thead {
    /*border:none !important;*/
}

td {
    /*border:none !important;*/
    height: 35px;
}

@media screen {
    .bgSelect {
        background-color: #A4BCCE !important;
    }

    .dpl-table tr{
        background-color: white;
    }

    .dpl-table tr.hover, .dpl-table tr:hover {
        /*background-color: #034D79;*/
        background-color: #99bcce !important;
        color: black;
    }

    .dpl-table td {
        padding: 6px 5px !important;
    }

    .dpl-table td:nth-child(1) {
        padding: 6px 0px !important;
    }

    .dpl-table > thead > tr > td {
        /*border-bottom: 5px solid #99BCCE !important;*/
        /*border-right: 5px solid #99BCCE !important;*/
        /*border-left: 5px solid #99BCCE;*/
        /*border-top: 5px solid #99BCCE;  */
        border-bottom: 5px solid #BAD0DB !important;
        border-right: 5px solid #BAD0DB !important;
        border-left: 5px solid #BAD0DB;
        border-top: 5px solid #BAD0DB;
        background: #00152C;
    }

    .dpl-table > thead > tr > td:nth-child(1), .dpl-table > tbody > tr > td:nth-child(1) {
        /*background-color: #F0F5FA !important;*/
        background-color: #BAD0DB !important;
        padding: 5px 0px 0px;
    }

    .dpl-table > thead > tr > td > pre {
        font-size: 14px;
        color: #ffffff;
        font-weight: bold;
        font-family: 'Open Sans', Arial;
        padding: 6px 4px;
    }

    .dpl-table > tbody > tr > td {
        border-bottom: 5px solid #BAD0DB !important;
        border-right: 5px solid #BAD0DB !important;
        border-left: 5px solid #BAD0DB;
    }

    .dpl-table > tbody > tr > .addCartBtn {
        border-bottom: none !important;
        border-right: 3px solid #BAD0DB !important;
        border-left: none !important;
        background-color: #00152c;
    }

    .dpl-table > tbody > tr > .exclamationMark {
        padding: 0px 0px 0px 3px !important;
    }

    .dpl-table > tbody > tr:nth-child(1) > td {
        border: 0px solid black !important;
    }

    .dpl-table > tbody > tr:nth-child(1) {
        display: none;
    }

    .dpl-table > tbody > tr.selected {
        background: #eddbb4 !important;
    }

    /*.dpl-table > tbody > tr.selected > td > pre{*/
    /*  color: white !important;*/
    /*}*/
    .dpl-table {
        /*background-color: white;*/
        width: 100%;
    }

    .dpl-table > tbody > tr > td > pre {
        font-size: 14px;
        color: black;
    }

    .dpl-table > tbody > tr > td:nth-child(3) > pre {
        text-align: center;
    }

    .dpl-table > tbody > tr > td:nth-child(4) > pre, .dpl-table > tbody > tr > td:nth-child(3) > pre {
        /*text-align: left;*/
        padding-right: 5px;
        padding-left: 5px;
    }

    .dpl-table > tbody > tr.hover > td > button, .dpl-table > tbody > tr:hover > td > button {
        background-color: inherit;
    }

    .dpl-table > tbody > tr:hover > td > pre, .dpl-table > tbody > tr.hover > td > pre, .dpl-table > tbody > tr > td > button.selected {
        color: black !important;
    }

    .dpl-table > tbody > tr > td {
        /*border-bottom: 1px solid white !important;*/
        /*border-top: 0 !important;*/
        /*  background-color: white;*/
    }

    /*.dpl-table > tbody > tr > td > button{
      color: white;
      width: 0px;
      height: 0px;
      background-color: inherit;
      border-width: 4px 0 4px 8px;
      border-style: solid;
      border-top-color: transparent;
      border-right-color: transparent;
      border-bottom-color: transparent;
      border-left-color: #034E7A;
    }*/
    .dpl-table > tbody > tr > td.selected > button, .dpl-table > tbody > tr > td > button.selected {
        color: white;
        width: 0px;
        height: 0px;
        background-color: inherit;
        border-width: 8px 4px 0;
        border-style: solid;
        border-top-color: #034E7A;
        border-right-color: transparent;
        border-bottom-color: transparent;
        border-left-color: transparent;
    }

    .dpl-table > tbody > tr > td:nth-child(2) > div {
        text-align: left;
    }
}

#dpl-holder {
    padding-top: 20px;
    padding-bottom: 20px;
}

#dpl {
    background-color: inherit !important;
}

.add_basket {
    width: 22px;
    height: 22px;
    color: #034D79;
}

.ContextMenuItem {
    color: #034D79 !important;
    font-family: 'Open Sans', Arial !important;
}

.ContextMenuDivider {
    background-color: #034D79 !important;
}

.MuiTypography-h5, .MuiTypography-h6 {
    color: white;
}

.btn-primary {
    background-color: #034D79 !important;
    border-color: #034D79 !important;
}

button:focus {
    outline: 0 !important;
}

#quantityBox {
    margin: 0px !important;
    width: 30px;
    height: 10px !important;
    border-style: none;
}

.tickBoxQuantity {
    margin-left: 10px
}

.triangleBtnRight {
    color: white;
    width: 0px;
    height: 0px;
    background-color: inherit;
    border-width: 5px 0 5px 8px;
    border-style: solid;
    border-top-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent;
    border-left-color: #00152C;
    margin-top: 6px !important;
}

.triangleBtnLeft {
    color: white;
    width: 0px;
    height: 0px;
    background-color: inherit;
    border-width: 5px 8px 5px 0;
    border-style: solid;
    border-top-color: transparent;
    border-right-color: #00152C;
    border-bottom-color: transparent;
    border-left-color: transparent;
    margin-top: 6px !important;
}

.transformTriangle {
    transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
}

.transformTriangleInverse {
    transform: rotate(-90deg);
    -webkit-transform: rotate(-90deg);
}

.td-none {
    background-color: white !important;
    border-style: none;
    padding-left: 5px !important;
    font-size: 14px !important;
}

.td-cart, .td-cart-header {
    border-bottom: 5px solid #BAD0DB !important;
    border-right: 5px solid #BAD0DB !important;
    border-left: 5px solid #BAD0DB;
    border-top: 5px solid #BAD0DB;
    background: white !important;
}

.td-cart-header {
    color: black;
    font-weight: bold !important;
}

.card-body {
    float: right !important;
    border: none !important;
    padding-right: 0 !important;
    padding: 0 0 1.25rem !important;
    width: auto;
    height: auto;

}

.card-body-total {
    margin-top: 20px;
    /*width: 125px;*/
    float: right;
    border: none !important;
    flex-direction: row !important;
}

.btn-cart-lower, .btn-cart-upper {
    margin: 5px;
    width: 100px;
}

.btn-cart-upper {
    margin-top: 25px !important;
}


.btn-primary {
    background-color: #00152C !important;
}

.btn-outlineprimary {
    background-color: grey !important;
    color: white !important;
    font-weight: bold;
}

.text-cart {
    margin: 10px !important;
    font-weight: bold;
}

.btn-danger {
    background-color: black !important;
    height: auto !important;
    width: auto !important;
    padding: 2px !important;
}

.delete-btn-div {
    float: left;
}

.cart-qty-input, .cart-qty-input.selected {
    text-align: center;
    border-style: none !important;

}

.container, .container-fluid, .container-sm, .container-md, .container-lg, .container-xl {
    padding-left: 3px !important;
    padding-right: 6px !important;
}

pre, code, kbd, samp {
    font-family: 'Open Sans', Arial !important;
}

#tab-content-0 {
    overflow-y: auto;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
    opacity: 1;
}

.MuiDialog-paperFullScreen {
    width: 100% !important;
}

td > pre {
    overflow-x: auto;
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -o-pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word;
}

.indent1 {
    padding-left: var(--indent-unit);
}

.indent2 {
    padding-left: calc((1 * 7px) + var(--indent-unit)) !important;
}

.indent3 {
    padding-left: calc((2 * 7px) + var(--indent-unit)) !important;
}

.indent4 {
    padding-left: calc((3 * 7px) + var(--indent-unit)) !important;
}

.indent5 {
    padding-left: calc((4 * 7px) + var(--indent-unit)) !important;
}

.indent6 {
    padding-left: calc((5 * 7px) + var(--indent-unit)) !important;
}

.indent7 {
    padding-left: calc((6 * 7px) + var(--indent-unit)) !important;
}

.indent8 {
    padding-left: calc((7 * 7px) + var(--indent-unit)) !important;
}

.indent9 {
    padding-left: calc((8 * 7px) + var(--indent-unit)) !important;
}